import React from 'react';
import PropTypes from 'prop-types';
import { v1 as uniqueKey } from 'uuid';
import { IconButton, Link } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import FooterLogo from '../images/icons/footer_logo.svg';
import TwitterIcon from '../images/icons/twitter.svg';
import FacebookIcon from '../images/icons/facebook.svg';
import LinkedInIcon from '../images/icons/linkedin.svg';
import GitHubIcon from '../images/icons/github.svg';
import XingIcon from '../images/icons/xing.svg';

const propTypes = {
  /**
   * List tabs configuration.
   */
  links: PropTypes.arrayOf(
    PropTypes.shape({
      /**
       * The link label.
       */
      label: PropTypes.string.isRequired,

      /**
       * href
       */
      href: PropTypes.string,
    }),
  ),
};

const defaultProps = {
  links: [
    {
      label: 'About geOps',
      href: 'https://geops.ch/about?language=en',
    },
    {
      label: 'Privacy Policy',
      href: 'https://geops.ch/datenschutz',
    },
    {
      label: 'Imprint',
      href: 'https://geops.ch/impressum',
    },
  ],
};

const useStyles = makeStyles((theme) => ({
  footer: {
    bottom: 0,
    left: 0,
    right: 0,
    display: 'flex',
    flexWrap: 'wrap',
    alignItems: 'center',
    justifyContent: 'space-between',
    backgroundColor: theme.colors.footer,
    padding: '0 30px',
    minHeight: 70,
    zIndex: 1100,
    [theme.breakpoints.down('sm')]: {
      minHeight: 100,
      flexDirection: 'column',
      alignItems: 'initial',
    },
  },
  footerElement: {
    display: 'flex',
    alignItems: 'center',
    margin: '15px 0',
  },
  brand: {
    minWidth: 200,
  },
  logo: {
    padding: 10,
    width: 80,
  },
  icon: {
    padding: 10,
  },
  svg: {
    width: 20,
    height: 20,
  },
  links: {
    [theme.breakpoints.down('sm')]: {
      display: 'flex',
      flexDirection: 'column',
    },
  },
  link: {
    marginLeft: 10,
    marginRight: 10,
    whiteSpace: 'nowrap',
    color: 'white',
    fontSize: 16,
  },
}));

const Footer = ({ links }) => {
  const classes = useStyles();
  return (
    <footer className={classes.footer}>
      <div className={`${classes.footerElement} ${classes.brand}`}>
        <img className={classes.logo} src={FooterLogo} alt="Logo" />
      </div>
      <div className={classes.links}>
        {Array.isArray(links) &&
          links.map((link) => (
            <Link
              key={uniqueKey()}
              href={link.href}
              className={classes.link}
              variant="body1"
            >
              {link.label}
            </Link>
          ))}
      </div>
      <div className={`${classes.footerElement}`}>
        <IconButton
          className={classes.icon}
          href="https://twitter.com/geops?lang=en"
          target="_blank"
          rel="noopener"
        >
          <img className={classes.svg} src={TwitterIcon} alt="twitter" />
        </IconButton>
        <IconButton
          className={classes.icon}
          href="https://www.facebook.com/geOpsSpatialWeb"
          target="_blank"
          rel="noopener"
        >
          <img className={classes.svg} src={FacebookIcon} alt="facebook" />
        </IconButton>
        <IconButton
          className={classes.icon}
          href="https://github.com/geops/"
          target="_blank"
          rel="noopener"
        >
          <img className={classes.svg} src={GitHubIcon} alt="github" />
        </IconButton>
        <IconButton
          className={classes.icon}
          href="https://de.linkedin.com/company/geops"
          target="_blank"
          rel="noopener"
        >
          <img className={classes.svg} src={LinkedInIcon} alt="linkedin" />
        </IconButton>
        <IconButton
          className={classes.icon}
          href="https://www.xing.com/companies/geops"
          target="_blank"
          rel="noopener"
        >
          <img className={classes.svg} src={XingIcon} alt="xing" />
        </IconButton>
      </div>
    </footer>
  );
};

Footer.propTypes = propTypes;
Footer.defaultProps = defaultProps;

export default Footer;
